







import { Component, Prop, Vue, Watch } from 'vue-property-decorator'

@Component
export default class Waterfall extends Vue {
  @Prop({default: () =>[], type: Array}) private list!: Array<any>
  @Prop({default: 7, type: Number}) private spacing!: number
  @Prop({default: 5, type: Number}) private column!: number
  @Prop({default: false, type: Boolean}) private loadChange!: boolean
  @Prop({default: 'parent', type: String}) private containerType!: string
  
  container:any = null
  screenWidth:number = 0
  throttling:boolean = true
  
  @Watch('loadChange')
  changeDo():void {
    this.initWaterfall()
  }

  get waterWidth():number {
    const { screenWidth, spacing, column } = this
    return (screenWidth - (column + 1) * spacing) / column
  }

  mounted() {
    this.container = this.containerType === 'parent'? this.$refs.water: document.documentElement
    this.screenWidth = this.container.clientWidth
    window.addEventListener('resize', this.winResize)
    this.$once('hook:beforeDestroy', ():void => {
      window.removeEventListener('resize', this.winResize)
    })
  }
  
  updated() {
    this.initWaterfall()
  }

  winResize():any {
    if( this.throttling ) {
      this.screenWidth = this.container.clientWidth
      this.throttling = false
      setTimeout(() => {
        this.initWaterfall()
        this.throttling = true
      }, 200)
    }
  }

  reload():void {
    this.screenWidth = this.container.clientWidth
    this.initWaterfall()
  }
  
  initWaterfall():void {
    const { spacing, column } = this
    let itemsTHeight:Array<number> = []
    let itemsX:Array<number> = []
    const waterBox:any = this.$refs.water
    const waterList:any = this.$refs.waterItem
    if( !!waterList ) {
      itemsTHeight = Array.from({length: column}, (temp:any) => spacing)
      itemsX = Array.from({length: column}, (temp:any, idx: number) => spacing + idx * (this.waterWidth + spacing))
      waterList.forEach((water:any, index:number) => {
        const maxTHeight:number = Math.min.apply(null, itemsTHeight)
        const columnIndex:number = itemsTHeight.indexOf(maxTHeight)
        water.style.cssText = `width:${this.waterWidth}px;transform:translate(${itemsX[columnIndex]}px, ${maxTHeight}px);`
        itemsTHeight[columnIndex] = maxTHeight + water.getBoundingClientRect().height + spacing
      })
      waterBox.style.height = `${Math.max.apply(null, itemsTHeight)}px`
    }
  }
}
